@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
/* FONT IMPORTS */

.icon {
  font-family: 'Material Icons', serif;
  font-style: normal;
  display: inline-block;
  vertical-align: middle;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: 'liga';
}


/* VARIABLES */

:root {
  --main-color: #1a8fff;
  --text-color: #777;
  --text-color-light: #ccc;
  --border-color: #eee;
  --bg-color: #f9f9f9;
  --neutral-color: #fff;
}


/* GENERAL */

* {
  box-sizing: border-box;
}

html, body, .App, #root {
  height: 100%;
}

body {
    margin: 0;
    font-family: 'Open Sans', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
    font-size: 1em;
    font-weight: 300;
    line-height: 1.5;
    color: #777;
    color: var(--text-color);
    background: #f9f9f9;
    background: var(--bg-color);
    position: relative;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

main {
  display: block;
  margin: 0 auto;
  margin-top: 0em;
  max-width: 50em;
}


/* GRID */

.row {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

/* .row-middle {
  align-items: center;
} */

.col {
  flex-grow: 1;
  flex-basis: 0;
  max-width: 100%;  /* OLD - iOS 6-, Safari 3.1-6, BB7 */  /* TWEENER - IE 10 */ /* NEW - Safari 6.1+. iOS 7.1+, BB10 */
  display: flex;         /* NEW, Spec - Firefox, Chrome, Opera */

}

/* .col-start {
  justify-content: flex-start;
  text-align: left;
} */

.col-center {
  justify-content: center;
  text-align: center;
}

/* .col-end {
  justify-content: flex-end;
  text-align: right;
} */
.login-page {
  background: linear-gradient(315deg, #4dccc6 0%, #96e4df 74%);
  min-height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
}

.login-page .logos {
  pointer-events: none;
  color: #333333;
  /* padding: 0em 0.3em; */
  /* box-shadow: 0px 0px 0.5em red; */
}

.login-page .logo-first-item {
  margin: 0em 0.3em;
  box-shadow: 0px 0px 0.5em #f7f7f7;
}

.login-page .logo-divider {
  margin: 0em 0.6em 0em 0.2em;
}

.login-page .row {
  padding: 1.3em;
}

@-webkit-keyframes fadeInDown {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
  }
}

@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.login-page .column {
  background: #f7f7f7;
  margin: 4rem;
  padding: 1rem 2rem;
  border: 1px grey solid;
  border-radius: 1rem 1rem;
  box-shadow: 0em 0em 0.55em grey;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;

  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;

  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
}

@media only screen and (max-width: 450px) {
  .login-page .row {
    padding: 1.3em 0rem;
  }
  .login-page .logo-first-item {
    margin: 0em 0.3em 0em -0.3em;
  }
  .login-page .column {
    margin: 3rem;
  }
}

