.calendar .header {
  text-transform: uppercase;
  overflow: hidden;
  font-weight: 700;
  font-size: 115%;
  border-bottom: 1px solid var(--border-color);
  background-color: #f1f1f1;
}

.calendar .header .header-title {
  margin: 1em 0em;
  padding-top: 0.3rem;
  /* border: 1px red dashed */
}

.calendar .header .icon {
  cursor: pointer;
  transition: 0.15s ease-out;
  margin: 0em;
  padding: 1.5em;
  /* border: 1px red dashed */
}

@media only screen and (min-width: 450px) {
  .calendar .header .icon:hover {
    transform: scale(1.75);
    transition: 0.25s ease-out;
    color: var(--main-color);
  }
}

@media only screen and (max-width: 450px) {
  .calendar .header {
    font-size: 90%;
  }
  .calendar .header .icon:active {
    transform: scale(1.75);
    transition: 0.01s ease-out;
    color: var(--main-color);
  }
}

@media only screen and (max-height: 500px) {
  .calendar .header .icon:active {
    transform: scale(1.75);
    transition: 0.01s ease-out;
    color: var(--main-color);
  }
}

@media only screen and (max-width: 350px) {
  .calendar .header {
    font-size: 79%;
  }
}

.calendar .days {
    text-transform: uppercase;
    font-weight: 400;
    color: var(--text-color-light);
    font-size: 70%;
    padding: .75em 0;
    border-bottom: 1px solid var(--border-color);
  }
.day-modal .icon {
  font-size: 1.45em;
  margin: 0rem 0.5rem;
}

.day-modal .time-range-picker {
  transition: 0.35s ease-in-out;
  border-radius: 0.3em 0.3em 0px 0px;
}

.day-modal .green {
  background: rgba(0, 255, 0, 0.15);
}

.day-modal .yellow {
  background: rgba(255, 255, 0, 0.2);
}

.calendar .body .cell {
  position: relative;
  height: 5em;
  border-right: 1px solid var(--border-color);
  overflow: hidden;
  cursor: pointer;
  background: var(--neutral-color);
  transition: 0.25s ease-in-out;
}

.calendar .body .cell:hover {
  background: var(--bg-color);
  transition: 0.25s ease-out;
}

.calendar .body .selected {
  border-top: 0px var(--border-color);
  border-bottom: 0px var(--border-color);
  border-left: 6px solid transparent;
  border-image: linear-gradient(45deg, #1a8fff 0%, #53cbf1 40%);
  border-image-slice: 1;
}

.calendar .body .cell:last-child {
  border-right: none;
}

.calendar .body .cell .number {
  position: absolute;
  top: 0.55em;
  right: 0.55em;
  font-weight: 700;
  font-size: 82.5%;
  text-align: center;
  width: 1.55em;
  border-radius: 0.25em;
}

.calendar .body .cell .yellow {
  background: rgba(255, 255, 0, 0.38);
  transition: 0.35s ease-in-out;
}

.calendar .body .cell .green {
  background: rgba(0, 255, 0, 0.18);
  transition: 0.35s ease-in-out;
}

.calendar .body .cell .notifiers-area {
  position: absolute;
  font-weight: 0%;
  line-height: 1;
  bottom: 0.4em;
  right: 0.1em;
}

.calendar .body .cell .notifiers-area .icon {
  margin: 0rem 0.2rem 0rem 0rem;
}

.calendar .body .disabled {
  color: var(--text-color-light);
  pointer-events: none;
}

.calendar .body .closed {
  background: linear-gradient(
    225deg,
    var(--text-color-light) 5%,
    var(--bg-color) 35%
  );
  pointer-events: none;
}

.calendar .body .cell .bg {
  font-weight: 700;
  line-height: 1;
  color: var(--main-color);
  opacity: 0;
  font-size: 8em;
  position: absolute;
  top: -0.2em;
  right: -0.05em;
  transition: 0.25s ease-out;
  letter-spacing: -0.07em;
}

@media only screen and (max-width: 600px) {
  .bg {
    display: none;
  }
  .notifiers-area .icon {
    font-size: smaller;
  }
}

@media only screen and (max-width: 350px) {
  .notifiers-area .icon {
    font-size: 0.7em;
  }
}

@media only screen and (max-height: 639px) {
  .calendar .body .cell {
    height: 4.3em;
  }
}

@media only screen and (max-height: 450px) {
  .bg {
    display: none;
  }
  /* .calendar .body .cell {
    height: 3.4em;
  } */
}

.calendar .body .cell:hover .bg,
.calendar .body .selected .bg {
  opacity: 0.05;
  transition: 0.25s ease-in;
}

.calendar .body .row {
  border-bottom: 1px solid var(--border-color);
}

.calendar .body .row:last-child {
  border-bottom: none;
}

.calendar .body .col {
  flex-grow: 0;
  flex-basis: calc(100% / 7);
  width: calc(100% / 7);
}

.calendar {
  display: block;
  position: relative;
  width: 100%;
  background: var(--neutral-color);
  border: 1px solid var(--border-color);
}

